.button {
    height: 4em;
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: darkgrey;
    font-size: 1.6em;
    color: white;
    flex: 1;
    cursor: pointer;
    user-select: none;
}

.double {
    flex-grow: 2;
}

.button:hover {
    animation: hoverAnim 700ms forwards;
}

@keyframes hoverAnim {
    to {
        background-color: cornflowerblue;
        font-weight: bolder;
    }
}
