.app {
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
    align-items: center;
    width: 100%;
    height: 100vh;
    flex-direction: column;
}

.calc {
    width: 400px;
    height: 600px;
    overflow: hidden;
    border-radius: 1em;
}

.row {
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
    width: 100%;
}

.classWrapper {
    width: 600px;
    display: flex;
    flex-direction: column;
    max-height: 70vh;
    overflow: scroll;
    border: 1px solid black;
    padding: 20px 30px 0 30px;
}

.class, .assignment {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    min-height: 80px;
}

button.add {
    width: 20%;
    align-self: flex-end;
    background-color: cornflowerblue;
    border: none;
    color: white;
    font-size: 1em;
    cursor: pointer;
}

button.add, button.compute {
    background-color: white;
    border: none;
    color: cornflowerblue;
    min-height: 35px;
    font-size: 1em;
    cursor: pointer;
    margin-top: 20px;
    border: 2px solid cornflowerblue;
    font-weight: bold;
    margin-bottom: 20px;
}

button.add:hover, button.compute:hover {
    background-color: cornflowerblue;
    color: white;
}

*:focus {
    outline: none;
}
.gpa, .grade {
    align-self: flex-start;
    display: flex;
    flex-direction: column;
}

input {
    border-radius: .5em;
    border: 1px solid grey;
    height: 25px;
    font-size: 1em;
}

select {
    height: 30px;
    font-size: 1em;
    border-radius: .5em;
}
