footer {
    display: block;
    margin-top: auto;
    background-color: lightgrey;
    width: 100%;
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
    align-items: center;
    height: 10vh;
}

.link {
    color: cornflowerblue;
    font-size: 1.4em;
    font-weight: bold;
    text-align: center;
    width: 33vw;
    cursor: pointer;
}

.link:hover {
    text-decoration: underline;
}

.link a:visited {
    color: inherit;
}

.link a {
    cursor: pointer;
    text-decoration: none;
}
